import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import sahil from "../images/sahils5.jpg"

export default function Home({ data }) {
  return (
    <Layout>
      <h2>Hey!</h2>
      <div>
      <figure style={{ textAlign: "center"}}>
          <img src={sahil} alt="Sahil Shembekar" width="300" height="300"/>
          {/* <figcaption><a href="https://www.google.com">Some Caption</a></figcaption> */}
        </figure>
      </div>
      <p>
        I am Sahil. I am a Graduate student at <a href="https://www.cse.sci.waseda.ac.jp/department-en/mmech/" target="_blank">Waseda University</a> specializing in 
        Robotics and, I also work as a 
        Research Assistant at <a href="https://www.sonycsl.co.jp/" target="_blank">Sony CSL</a> on the Superception research framework.
        At Waseda, I am advised by Professor <a href="https://waseda.pure.elsevier.com/en/persons/shigeki-sugano" target="_blank"> 
        Shigeki Sugano</a> and co-advised by Associate Professor <a href="https://waseda.pure.elsevier.com/en/persons/mitsuhiro-kamezaki" target="_blank">Mitsuhiro Kamezaki</a>.
        </p>
        
        <p>  
        Before this, I was at <a href="https://tx-inc.com/en/home/" target="_blank">Telexistense Inc</a> working on their flagship robot (Model T) to develop next-generation robotic end effectors.
        In the past, I worked at <a href="https://whill.inc/jp/" target="_blank">Whill Inc</a> on robot localization and path planning for the intelligent personal EV (Electric Vehicle) Model C.
        I have also briefly worked as an application development associate at <a href="https://www.accenture.com/in-en" target="_blank">Accenture Technologies Pvt Ltd</a>.
        </p>

        <p>  
        My research is in the field of robot end effectors, robot actuation systems, and tactile sensors with the focus on making robots 
        more safe, versatile, and intelligent. I work on mechanical design, electronic design, simulation, and 
        sensor-robot integration associated with the tactile sensors and robots.
        </p>
        <p>
          <h3> Contact </h3> 
          <p>
          Feel free to connect with me on <a href="mailto:sahil.shembekar@gmail.com" target="_blank">Email</a>, <a href="https://www.linkedin.com/in/sahil-shembekar/" target="_blank">
            LinkedIn</a> or <a href="https://twitter.com/sahilshems" target="_blank">Twitter</a>.
          </p>
        </p>
    </Layout>
  )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`